.popup-youtube {
    position: relative;
}

.popup-youtube i{
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    background: rgba(0, 0, 0, 0.66);
    padding: 28px 29px;
    color: #fff;
    margin: -31px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
}


/* Portfolio Hover  */

.single_item figure {
    overflow: hidden;
}

.single_item figure figcaption{
    position: absolute;
    top:0;
    text-align: center;
    width: 90%;
    height: 100%;
    opacity: 0;
    transform: translateY(30px);
    -webkit-transform: translateY(30px);
    -moz-transform: translateY(30px);
    transition: all ease 0.4s;
    -webkit-transition: all ease 0.4s;
    -moz-transition: all ease 0.4s;
}

.single_item figure figcaption .outter{
    display: table;
    width: 100%;
    height: 100%;
}

.single_item figure figcaption .inner{
    display: table-cell;
    vertical-align: middle;
}

.single_item figure figcaption span{
    color: rgba(0, 0, 0, 0.80);
    font-size: 13px;
}

.single_item figure figcaption .title{
    color: #000;
    font-size: 22px;
    font-weight: bold;
}

.single_item:hover > figure img{
  filter: blur(8px);
  -webkit-filter: blur(8px);
  opacity: .7;
}

.single_item:hover > figure figcaption{
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    transition: all ease 0.4s;
    -webkit-transition: all ease 0.4s;
    -moz-transition: all ease 0.4s;
}

.single_item.popup-youtube:hover > i{
    opacity: 0;
    transition: all ease 0.3s;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
}




