/*max Width 992px */

@media screen and (max-width: 991px) {

    .home-content h1{
        font-size: 50px;
    } 
    
     .what-can .feature{
        padding: 0 30px;
    }
    
    .about.type-1 .about-image{
        background: none;
    }
    
    .about.type-1 .about-image img{
        margin: 0;
    }
    
}


/*max Width 768px */

@media screen and (max-width: 767px) {


    
    .home-content h1{
        font-size: 32px;
        margin-bottom: 10px;
    }
    
    .contact-info{
        margin-bottom: 30px;
        padding: 0 50px;
    }
   
    .social a{
        margin: 0 10px;
        font-size: 12px;
    }
    
    nav{
        padding: 10px 20px;
    }
    
    .nav-menu{
        margin-top: 40px;
        display: none;
        float: none;
        width: 100%;
        padding-left: 0px;
    }
    
    .nav-menu li{
        /* float: none;  */
        width: 100%;
        text-align: center;
        height: 45px;
        border-top: 1px solid #f7f7f7;
        line-height: 45px;
    }
   
    .responsive{
        float: right;
        display: block;
    }
    
    .about {
    padding-top: 60px;
    }
    
    .about h2 {
        margin-top: 30px;
    }
    
    .gray-bg {
        margin-top: 30px;
    }
    
    .what-can {
        padding: 45px 0;
    }
    
    .section-title {
        padding: 60px 0 45px 0;
    }
    
    .portfolio_filter ul{
        margin-top: 15px;
    }
    
    .portfolio_filter ul li{
        height: 30px;
    }
    
    .diagonal, .diagonal-gray, .diagonal-white{
        height: 70px;
    }
    
    .row.bottom_90{
        margin-bottom: 0;
    }
    
    #root .blog{
        padding-bottom: 60px;
    }
    
}


/*min Width 768px */
@media screen and (min-width: 768px) {
    
 .nav-menu{
        display: block!important;
    }
    
}


